import {useContext} from 'react';
import Utils from '../Utils';
import UserContext from '../contexts/UserContext';
import moment from 'moment';
 
function AppHeader() {
  const {logout, userData} = useContext(UserContext);

  const logAdminOut = (e) => {
    e.preventDefault();
    logout(() => {
      window.location.href = process.env.PUBLIC_URL + "/login";
    });  
  }


  return (
    <nav className="main-header navbar navbar-expand navbar-white navbar-light">
    <ul className="navbar-nav">
      <li className="nav-item">
        <a className="nav-link" data-widget="pushmenu" href="#" role="button"><i className="fas fa-bars"></i></a>
      </li>
      <li className="nav-item d-none d-sm-inline-block">
        <a href={process.env.PUBLIC_URL} className="nav-link">Home</a>
      </li>
      <li className="nav-item d-none d-sm-inline-block">
        <a href="#" className="nav-link">Contact</a>
      </li>
    </ul>

    <ul className="navbar-nav ml-auto">
      {Utils.isLoggedIn() && <>
        <li className="nav-item dropdown user-menu">
          <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown">
            <img src={`${process.env.PUBLIC_URL}/dist/img/user2-160x160.jpg`} className="user-image img-circle elevation-2" alt="User Image" />
            <span className="d-none d-md-inline">{userData.username}</span>
          </a>
          <ul className="dropdown-menu dropdown-menu-lg dropdown-menu-right">
            <li className="user-header bg-primary">
              <img src={`${process.env.PUBLIC_URL}/dist/img/user2-160x160.jpg`} className="img-circle elevation-2" alt="User Image" />

              <p>
                {userData.username} - {userData.role}
                <small>Member since {moment(userData.created_at).format('MMM Y')}</small>
              </p>
            </li>
            <li className="user-footer">
              <a href="#" className="btn btn-default btn-flat">Profile</a>
              <a className="btn btn-default btn-flat float-right" onClick={logAdminOut}>Sign out</a>
            </li>
          </ul>
        </li>

        <li className="nav-item">
          <a className="nav-link" data-widget="control-sidebar" data-slide="true" onClick={logAdminOut}>
            <i className="fas fa-sign-out-alt" />
          </a>
        </li>
      </>}
           
      <li className="nav-item">
        <a className="nav-link" data-widget="fullscreen" href="#" role="button">
          <i className="fas fa-expand-arrows-alt"></i>
        </a>
      </li>
      
    </ul>
  </nav>
  );
}
 
export default AppHeader;