import {LoginLayout,DefaultLayout} from './layouts';

import {Login,Dashboard,Role,Admin, Users, Sociallink, ContactList, TrainerCourses, TrainerBlogs} from './pages';

import Utils from './Utils';

const CustomRoutes = [  
  
  {
    'path'      : '/login', 
    'secure'    : false,    
    'exact'     : true,
    'layout'    : LoginLayout, 
    'component' : Login
  },
  {
    'path'      : '/roles',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Role,
    'accessible': Utils.canAccess('roles')
  },
  {
    'path'      : '/sociallinks',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Sociallink,
    'accessible': Utils.canAccess('roles')
  },
  {
    'path'      : '/admins',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Admin,
    'accessible': Utils.canAccess('admin')
  },
  {
    'path'      : '/users',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Users,
    'accessible': Utils.canAccess('users')
  },
  {
    'path'      : '/contact',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : ContactList
  },
  {
    'path'      : '/courses',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : TrainerCourses
  },
  {
    'path'      : '/blogs',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : TrainerBlogs
  },
  {
    'path'      : '/',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  },
  {
    'path'      : '*',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  }
];

export default CustomRoutes;