import React,{useState,useEffect} from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _, { filter } from 'lodash';

const Users = (props) => {

  const [list,setList] = useState({loading: true, error: false, pageInfo: {}, data: []});
  const [filters, setFilters] = useState({where: {role_id: process.env.REACT_APP_TRAINER_ROLE}, limit: 15, start: 0});
  const [showUser,setShowUser] = useState(false); // id or false

  const deleteRecord = (id)  => (e) => {
    if(window.confirm("You are going to delete record, are you sure?")){
      axios.delete(Utils.apiUrl(`role/${id}`),Utils.apiHeaders())
      .then(res => {
        fetchList();
        window.alert(res.data.message);
      })
    }
  };

  const fetchList = () => {
    setList({...list, loading: true});

    let params = `?limit=${filters.limit}&start=${filters.start}&`;
    params += _.map(filters.where, (v,k) => `where[${k}]=${v}`).join('&');
    axios.get(Utils.apiUrl('user/list' + params),Utils.apiHeaders())
    .then(res => {
      if(res.data.success){
        setList({...list, loading: false, error: false, pageInfo: res.data.pageInfo, data: res.data.data});
      }else{
        setList({...list, loading: false, error: res.data.message, pageInfo: {}, data: []});
      }
    })
  };

  useEffect(fetchList,[filters]);

  const gotoPage = (page) => (e) => {
    const start = (page-1) * filters.limit;
    setFilters({...filters, start: start});
  };

  const toggleActive = (id) => (e) => {
    if(window.confirm(`You are going to ${e.target.checked ? "activate" : "de-activate"} this user, are you sure?`)){
      axios.put(Utils.apiUrl(`user/edit/${id}`),`active=${e.target.checked ? 1 : 0}`,Utils.apiHeaders())
      .then(res => {
        window.alert(res.data.message);
        fetchList();        
      })
    }
  };

  const renderRow = (record) => {
    let profileURL = process.env.REACT_APP_SITE_URL;
    
    if(parseInt(record.role_id)===parseInt(process.env.REACT_APP_TRAINER_ROLE)){
      profileURL += `/trainers/${record.slug}`;
    }else{
      profileURL += `/student-profile/${record.id}`;
    }

    return <tr key={record.id}>
      <td>{record.id}</td>
      <td>{record.firstname} {record.middlename} {record.lastname}</td>
      <td>{record.email}</td>
      <td>{record.mobile}</td>
      <td>{Utils.dateTime(record.created_at)}</td>
      <td className="d-flex">
          <a className="btn p-0 mr-2" target="_blank" href={profileURL}>
            <i className="fas fa-search"></i>
          </a> 
          <div className="custom-control custom-switch">
            <input type="checkbox" className="custom-control-input" id={`active${record.id}`} name="active" value="1" defaultChecked={record.active} onChange={toggleActive(record.id)} />
            <label className="custom-control-label" htmlFor={`active${record.id}`}></label>
          </div>              
      </td>    
    </tr>;
  }
  
  const renderList = () => {
    return <table id="example1" className="table table-bordered table-striped">
      <thead>
      <tr>
        <th>#ID</th>
        <th>Name</th>
        <th>Email</th>
        <th>Mobile</th>
        <th>Registered On</th>
        <th>Action</th>
      </tr>
      </thead>
      <tbody>
        {list.data.map(record => renderRow(record))}
      </tbody>
    </table>    
  };

  return (<div>
    <div className="content-wrapper">
      <div className="content-header">
        <div className="container-fluid">
          <div className="row mb-2">
            <div className="col-sm-6">
              <h1 className="m-0 text-dark">User Management</h1>
            </div>
            <div className="col-sm-6">
              <ol className="breadcrumb float-sm-right">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
                <li className="breadcrumb-item active">User Management</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <section className="content">
        <div className="container-fluid">
          <div className="row">
           <section className="col-lg-12 connectedSortable">
              <div className="card">                
                <div className="card-body">

                  <ul className="nav nav-pills mb-2">
                    <li className="nav-item"><a href="#" 
                      className={`nav-link ${filters.where.role_id===process.env.REACT_APP_TRAINER_ROLE ? 'active' : ''}`} 
                      onClick={() => setFilters({...filters, where: {role_id: process.env.REACT_APP_TRAINER_ROLE}})}>Trainers</a></li>
                    <li className="nav-item"><a href="#" 
                      className={`nav-link ${filters.where.role_id===process.env.REACT_APP_STUDENT_ROLE ? 'active' : ''}`} 
                      onClick={() => setFilters({...filters, where: {role_id: process.env.REACT_APP_STUDENT_ROLE}})}>Students</a></li>
                  </ul>
                  
                  {list.loading && <div className="text-center p-3">
                      Loading data.. <div className="spinner-grow text-danger"></div>
                    </div>}
                  {!list.loading && <>
                    {list.data.length === 0 && <div className="text-center bg-light p-3">No Record found!</div>}
                    {list.data.length > 0 && renderList()}
                    {list.data.length > 0 && Utils.showPagination({...list.pageInfo, ..._.pick(filters,['start','limit'])}, gotoPage)}
                  </>}
                </div>
              </div>
            </section>
          </div>
        </div>
      </section>

    
    </div>
  </div>)
};

export default Users;