import React,{useState, useEffect} from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _ from 'lodash';

const SocialForm = (props) => {

   const [data,setData] = useState(props.rec);
   const [loading, setLoading] = useState(false);

   const submitForm = (e) => {
     const frm = e.currentTarget;
     e.preventDefault();
 
     let frmdata = new FormData(frm);

     if(props.mode===2){
      frmdata.set('active',frm.active.checked ? "1" : "0");
     }

     (props.mode===1 ? axios.post : axios.put)(Utils.apiUrl(`sociallink/${props.mode===1 ? 'add' : `edit/${data.id}`}`),frmdata,Utils.apiHeaders())
     .then(res => {
        if(res.data.success){
           window.alert("Record saved successfully!");
           props.onSubmit();
           props.onClose();
        }else{
           window.alert(res.data.message);
        }
     }) 
 
   };
 
   
   return (<div className="modal fade show d-block" data-backdrop={true}>
      <div className="modal-dialog">
         <div className="modal-content">

            <form onSubmit={submitForm}>
                  
               <div className="modal-header">
                  <h4 className="modal-title">{props.mode===2 ? 'Edit' : 'Add'} Social Links</h4>
                  <button type="button" className="close" onClick={props.onClose}>&times;</button>
               </div>
               <div className="modal-body">
                  
                  {loading !== false && <div className="alert alert-light p-5 text-center">{loading} <div className="spinner-grow text-danger"></div></div>}
                  
                  {loading === false && <>

                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Social Link Title</label>
                        <input type="text" name="title" required className="form-control" placeholder="Enter Social Link Name" defaultValue={_.get(data,'title','')} />
                     </div>
                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Social Icon Class</label>
                        <input type="text" name="class" required className="form-control" placeholder="Enter Social Icon Class" defaultValue={_.get(data,'class','')} />
                     </div>
                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Social Link</label>
                        <input type="text" name="link" required className="form-control" placeholder="Enter Social Link" defaultValue={_.get(data,'link','')} />
                     </div>                     
                     <div className="custom-control custom-switch">
                        <input type="checkbox" className="custom-control-input" id={`active${props.id}`} name="active" value="1" defaultChecked={_.get(data,'active',true)} />
                        <label className="custom-control-label" htmlFor={`active${props.id}`}> Active State</label>
                     </div>
                     
                  </>}
               </div>

               {loading === false && <div className="modal-footer">
                  <button type="submit" className="btn btn-primary">Submit</button>
                  <button type="button" className="btn btn-secondary" onClick={props.onClose}>Close</button>
               </div>}

            </form>
         </div>
      </div>
   </div>);

};

export default SocialForm;