import React,{useContext,useEffect} from 'react';
import UserContext from '../contexts/UserContext';
import Utils from './../Utils';
import _ from 'lodash';

const Menu = (props) => {
  
  const {userData} = useContext(UserContext);


  const isMenuActive = (pth, active = true) => {
    const mclass = active ? 'active' : 'menu-open';
    if(pth === 'dashboard' && window.location.pathname===process.env.PUBLIC_URL){ //=== for dashboard
      return mclass;
    }else if(_.isArray(pth)){
      return _.find(pth, p => window.location.pathname.indexOf(process.env.PUBLIC_URL+'/'+p)> -1) ? mclass : '';
    }else{
      return window.location.pathname.indexOf(process.env.PUBLIC_URL+'/'+pth) > -1 ? mclass : '';
    }
  };

  useEffect(() => {

    const $ = window.jQuery;
    $('.has-treeview > a.nav-link').on('click',(evt) => {
      evt.preventDefault();
      var e = evt.target;
      $(e).parent().siblings().removeClass('menu-open');
      $(e).addClass('active').parent().addClass('menu-open');
      
    });

  },[]);
  return (<div>
    <aside className="main-sidebar sidebar-dark-primary elevation-4">
      <a href={process.env.PUBLIC_URL} className="brand-link">
        <img src={`${process.env.PUBLIC_URL}/dist/img/logo.png`} alt="AutoDidact Logo" className="brand-image elevation-3" style={{opacity: '.8'}} />
        <span className="brand-text font-weight-light">Admin</span>
      </a>
      <div className="sidebar">
        <div className="user-panel mt-3 pb-3 mb-3 d-flex">
          <div className="info">
            <a href={process.env.PUBLIC_URL} className="d-block">{userData.username}({userData.role})</a>
          </div>
        </div>
        <nav className="mt-2">
          <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            <li className="nav-item">
              <a href={process.env.PUBLIC_URL} className={`nav-link ${isMenuActive('dashboard')}`}>
                <i className="nav-icon fas fa-tachometer-alt" />
                <p>
                  Dashboard
                  <i className="right fas fa-angle-left" />
                </p>
              </a>
            </li>
            <li className={`nav-item has-treeview ${isMenuActive(['roles','admins','users'],false)}`}>
              <a href={process.env.PUBLIC_URL} className={`nav-link  ${isMenuActive(['roles','admins','users'])}`}>
                <i className="nav-icon fas fa-user-cog"></i>
                <p>
                  My Account
                  <i className="fas fa-angle-left right"></i>
                </p>
              </a>
              <ul className="nav nav-treeview">
                {Utils.canAccess('roles') && <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/roles`} className={`nav-link  ${isMenuActive('roles')}`}>
                    <i className="fas fa-user-check nav-icon"></i>
                    <p>Roles Management</p>
                  </a>
                </li>}
                {Utils.canAccess('admin') && <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/admins`} className={`nav-link  ${isMenuActive('admins')}`}>
                    <i className="fas fa-user-circle nav-icon"></i>
                    <p>Admin Management</p>
                  </a>
                </li>}
                {Utils.canAccess('user') && <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/users`} className={`nav-link  ${isMenuActive('users')}`}>
                    <i className="far fa-user-circle nav-icon"></i>
                    <p>Users Management</p>
                  </a>
                </li>}
              </ul>
            </li>
            <li className={`nav-item has-treeview ${isMenuActive(['contact','lead'],false)}`}>
              <a href={process.env.PUBLIC_URL} className={`nav-link  ${isMenuActive(['contact','lead'])}`}>
                <i className="nav-icon fas fa-money-check-alt" />
                <p>
                  Lead Management
                  <i className="fas fa-angle-left right" />
                  <span className="badge badge-info right">6</span>
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/contact`} className={`nav-link  ${isMenuActive('contact')}`}>
                  <i class="far fa-address-card nav-icon" /> 
                    <p>Contact Form</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href={process.env.PUBLIC_URL} className={`nav-link  ${isMenuActive('lead')}`}>
                  <i class="far fa-address-card nav-icon" /> 
                    <p>SEO Campaign</p>
                  </a>
                </li>
              </ul>
            </li>

            <li className="nav-item has-treeview">
              <a href={process.env.PUBLIC_URL} className="nav-link">
                <i className="nav-icon fas fa-money-check-alt" />
                <p>
                  Sales Items
                  <i className="fas fa-angle-left right" />
                  <span className="badge badge-info right">6</span>
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href={process.env.PUBLIC_URL} className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Order Items</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href={process.env.PUBLIC_URL} className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Invoice</p>
                  </a>
                </li>
              </ul>
            </li>
            <li className={`nav-item has-treeview ${isMenuActive(['courses','blogs'],false)}`}>
              <a href={process.env.PUBLIC_URL} className="nav-link">
                <i className="nav-icon fa fa-cube" />
                <p>
                  Catalog
                  <i className="right fas fa-angle-left" />
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/courses`} className={`nav-link  ${isMenuActive('courses')}`}>
                    <i className="far fa-circle nav-icon" />
                    <p>Products</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/blogs`} className={`nav-link  ${isMenuActive('blogs')}`}>
                    <i className="far fa-circle nav-icon" />
                    <p>Blogs</p>
                  </a>
                </li>
              </ul>
            </li>
            <li className="nav-item has-treeview">
              <a href={process.env.PUBLIC_URL} className="nav-link">
                <i className="nav-icon fas fa-layer-group" />
                <p>
                  Content
                  <i className="fas fa-angle-left right" />
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href="pages/UI/general.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Pages</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href="pages/UI/icons.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Block</p>
                  </a>
                </li>
                {Utils.canAccess('sociallinks') && <li className="nav-item">
                  <a href={`${process.env.PUBLIC_URL}/sociallinks`} className={`nav-link  ${isMenuActive('sociallinks')}`}>
                    <i className="fas fa-user-check nav-icon"></i>
                    <p>Social Links</p>
                  </a>
                </li>}
              </ul>
            </li>
            <li className="nav-item has-treeview">
              <a href={process.env.PUBLIC_URL} className="nav-link">
                <i className="nav-icon fas fa-blog" />
                <p>
                  Blog
                  <i className="fas fa-angle-left right" />
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href="pages/forms/general.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Categories</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href="pages/forms/advanced.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Posts</p>
                  </a>
                </li>
              </ul>
            </li>
            <li className="nav-item has-treeview">
              <a href={process.env.PUBLIC_URL} className="nav-link">
                <i className="nav-icon fas fa-sliders-h" />
                <p>
                  Reports
                  <i className="fas fa-angle-left right" />
                </p>
              </a>
              <ul className="nav nav-treeview">
                <li className="nav-item">
                  <a href="pages/tables/simple.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>Simple Tables</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href="pages/tables/data.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>DataTables</p>
                  </a>
                </li>
                <li className="nav-item">
                  <a href="pages/tables/jsgrid.html" className="nav-link">
                    <i className="far fa-circle nav-icon" />
                    <p>jsGrid</p>
                  </a>
                </li>
              </ul>
            </li>

          </ul>
        </nav>
      </div>
    </aside>    
  </div>)
};

export default Menu;
