import React  from 'react';
import _ from 'lodash';
import './App.css';
import CustomRoutes from './CustomRoutes';

import { BrowserRouter as Router, Route, Routes} from "react-router-dom";

import {UserProvider} from './contexts/UserContext';
import Utils from './Utils';
import {Login} from './pages';
import { LoginLayout} from './layouts';


const App = (props) => {

  const RoutedLayout = () => {
    return <Router basename={process.env.REACT_APP_BASENAME || ""}>
      <Routes>
        {CustomRoutes.map((route, index) => {
          if(route.secure && !Utils.isLoggedIn()){
            return <Route key={index} path={`${process.env.PUBLIC_URL}/`} element={<LoginLayout>
            <Login />
          </LoginLayout>} />
          }else if(!_.get(route,'accessible',true)){
            return <Route element={<h1>INVALID ACCESS!</h1>}></Route>
          }else{
            return (
              <Route
                key={index}
                path={process.env.PUBLIC_URL + route.path}
                exact={route.exact || false}
                element={<route.layout {...props}>
                      <route.component {...props} />
                    </route.layout>}
              />
            );
          }
        })}

		    <Route component={() => <h1>404 Not Found</h1>}></Route>
      </Routes>
    </Router>
  }
  
  const Provider = () => {
    return <UserProvider>
      <RoutedLayout />
    </UserProvider>;
  }

  return <Provider />;
}

export default App;
