import React,{useState,useEffect} from 'react';
import Utils from './../Utils';
import axios from 'axios';
import SocialForm from './SocialForm';
import _ from 'lodash';

const Sociallink = (props) => {

  const [list,setList] = useState({loading: false, error: false, pageInfo: {}, data: []});
  const [showForm,setShowForm] = useState({rec: false, mode: 0}); // 0=do not show, 1=add, 2=edit

  const deleteRecord = (id)  => (e) => {
    if(window.confirm("You are going to delete record, are you sure?")){
      axios.delete(Utils.apiUrl(`sociallink/${id}`),Utils.apiHeaders())
      .then(res => {
        fetchList();
        window.alert(res.data.message);
      })
    }
  };

  const fetchList = () => {
    setList({...list, loading: true})
    axios.get(Utils.apiUrl('sociallink/list'),Utils.apiHeaders())
    .then(res => {
      if(res.data.success){
        setList({...list, loading: false, error: false, pageInfo: res.data.pageInfo, data: res.data.data});
      }else{
        setList({...list, loading: false, error: res.data.message, pageInfo: {}, data: []});
      }
    })
  };

  useEffect(fetchList,[]);

  const renderList = () => {
    return <table id="example1" className="table table-bordered table-striped">
      <thead>
      <tr>
        <th>#ID</th>
        <th>Title</th>
        <th>Class</th>
        <th>Link</th>
        <th>Status</th>
        <th>Preview</th>
        <th>Action</th>
      </tr>
      </thead>
      <tbody>
        {list.data.map(record => <tr key={record.id}>
          <td>{record.id}</td>
          <td>{record.title}</td>
          <td>{record.class}</td>
          <td>{record.link}</td>
          <td>{record.active === 1 ? 'Active' : 'In-active'}</td>
          <td className="bg-dark text-center">
            <a href={_.get(record,'link','')} title={_.get(record,'title','')} target="_blank"><i className={_.get(record,'class','')}></i></a>
          </td>
          <td>
              <button className="btn btn-info btn-sm" onClick={() => setShowForm({...showForm,mode:2,rec:record})}>
                  <i className="fas fa-pencil-alt"></i>
              </button>&nbsp;
              <button className="btn btn-danger btn-sm" onClick={deleteRecord(record.id)}>
                  <i className="fas fa-trash"></i>
              </button>
          </td>    
        </tr>)}
      
      </tbody>
    </table>
  };

  return (<div>
    <div className="content-wrapper">
      <div className="content-header">
        <div className="container-fluid">
          <div className="row mb-2">
            <div className="col-sm-6">
              <h1 className="m-0 text-dark">Social Link Management</h1>
            </div>
            <div className="col-sm-6">
              <ol className="breadcrumb float-sm-right">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
                <li className="breadcrumb-item active">Social Link Management</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <section className="content">
        <div className="container-fluid">
          <div className="row">
           <section className="col-lg-12 connectedSortable">
              <div className="card">
                <div className="card-header">
                  <h3 className="card-title">Social Link List #</h3>
                  <span className="btn float-right">
                    <button className="btn btn-success btn-sm" onClick={()=>setShowForm({mode: 1, id: false})}>Add Link <i className="fas fa-plus"></i></button>
                  </span>
                </div>
                <div className="card-body">
                {list.loading && <div className="text-center p-3">
                      Loading data.. <div className="spinner-grow text-danger"></div>
                    </div>}
                  {!list.loading && <>
                    {list.data.length === 0 && <div className="text-center bg-light p-3">No Record found!</div>}
                    {list.data.length > 0 && renderList()}
                  </>}
                </div>
              </div>
            </section>
          </div>
        </div>
      </section>

      {showForm.mode > 0 && <SocialForm mode={showForm.mode} rec={showForm.rec} onClose={() => setShowForm({...showForm, mode: 0})} onSubmit={fetchList} />}
    
      
      </div>
  </div>)
};

export default Sociallink;