import React,{useState, useEffect} from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _ from 'lodash';

const AdminForm = (props) => {
   const [error,setError] = useState(false);
   const [data,setData] = useState({id:null, title: "", active: 1});
   const [loading, setLoading] = useState(false);
   const [list,setList] = useState({loading: false, error: false, pageInfo: {}, data: []});
   const fetchRecord = (id) => {
      setLoading('Fetching data..');
      axios.get(Utils.apiUrl(`admin/list?id=${id}`),Utils.apiHeaders())
      .then(res => {
         if(res.data.success && _.get(res,'data.data.0',false)){
            setData(res.data.data[0]);
            setLoading(false);
         }
      })
   };
   
   useEffect(() => {
      if(_.get(props,'id',0) > 0){
         fetchRecord(props.id);
      }
   },[]);
   
   const submitForm = (e) => {

      setError(false);
     const frm = e.currentTarget;
     e.preventDefault();
 
     let frmdata = new FormData(frm);

     if(props.mode===2){
      frmdata.set('active',frm.active.checked ? "1" : "0");
     }

     if(_.get(frm,'password.value',false)){
        if(frm.password.value!==frm.conf_password.value){
          setError("Password verification failed, please verify your password correctly!");
          return false;
        }
     }

     (props.mode===1 ? axios.post : axios.put)(Utils.apiUrl(`admin/${props.mode===1 ? 'add' : `edit/${data.id}`}`),frmdata,Utils.apiHeaders())
     .then(res => {
        if(res.data.success){
           window.alert("Record saved successfully!");
           props.onSubmit();
           props.onClose();
        }else{
           window.alert(res.data.message);
        }
     }) 
 
   };
 
   const renderRole = () => {
    return <select className="custom-select" id="role" >
           <option selected>Choose...</option>
           {list.data.map(record => <option value={record.id}>
            {record.title}
           </option>)}
        </select>  
  };

   return (<div className="modal fade show d-block" data-backdrop={true}>
      <div className="modal-dialog">
         <div className="modal-content">

            <form onSubmit={submitForm}>
                  
               <div className="modal-header">
                  <h4 className="modal-title">{props.mode===2 ? 'Edit' : 'Add'} Admin</h4>
                  <button type="button" className="close" onClick={props.onClose}>&times;</button>
               </div>
               <div className="modal-body">
                  
                  {loading !== false && <div className="alert alert-light p-5 text-center">{loading} <div className="spinner-grow text-danger"></div></div>}
                  
                  {loading === false && <>

                  {error && <div className="alert alert-danger p-3">{error}</div>}

                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Username</label>
                        <input type="text" name="username" required className="form-control" placeholder="Enter Username" defaultValue={_.get(data,'username','')} />
                     </div>
                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Email Address</label>
                        <input type="email" name="email" required className="form-control" placeholder="Enter email address" defaultValue={_.get(data,'email','')} />
                     </div>
                     
                     {props.mode==1 && <><div className="form-group">
                        <label htmlFor="exampleInputEmail1">New Password</label>
                        <input type="text" name="password" required className="form-control" placeholder="Enter New Password" />
                     </div>
                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Confirm Password</label>
                        <input type="password" name="conf_password" required className="form-control" placeholder="Verify your password" />
                     </div></>}

                     <div className="form-group">
                        <label>Module Access</label>
                        <div className="bg-light clearfix">
                           {_.map(Utils.rbacModules(),(rbac,mod) => <div className="m-2 float-left" key={mod}>
                              <input type="checkbox" id={mod} name="rbac[]" className="check-control" defaultChecked={_.get(data,'rbac',[]).includes(mod)} value={mod} />
                              <label htmlFor={mod} className="ml-2">{rbac}</label>
                           </div>)}
                        </div>
                     </div>
                     
                     {props.mode === 2 && <div className="custom-control custom-switch">
                        <input type="checkbox" className="custom-control-input" id={`active${props.id}`} name="active" value="1" defaultChecked={data.active} />
                        <label className="custom-control-label" htmlFor={`active${props.id}`}> Active State</label>
                     </div>}
                     
                  </>}
               </div>

               {loading === false && <div className="modal-footer">
                  <button type="submit" className="btn btn-primary">Submit</button>
                  <button type="button" className="btn btn-secondary" onClick={props.onClose}>Close</button>
               </div>}

            </form>
         </div>
      </div>
   </div>);

};

export default AdminForm;