import React,{createContext, useState} from 'react';

import axios from 'axios';

import Utils from './../Utils';

import _ from 'lodash';

const UserContext = createContext();

const UserProvider = (props) => {

  const [userData, setUserData] = useState(Utils.getUserData());
  const [stepData, setUCStepData] = useState({});

  const goLogin = ({user, pass},callback) => {    
    axios.post(Utils.apiUrl('admin/login'),`&user=${user}&pass=${pass}`,Utils.apiHeaders()).then(res => {      
      callback(res.data);      
      if(res.data.success){        
        setUserData(res.data.userData);        
        Utils.setUserData(res.data);            
      }
    }).catch(err => {
      callback(false, err.message);
    });
  }

  
  const logout = (callback) => {
    setUserData(false);
    Utils.setUserData(false); 
       
    callback();
  }

  
  return <UserContext.Provider value={{ userData, goLogin,logout}}>
    {props.children}
  </UserContext.Provider>;

}

export default UserContext;

export {UserProvider};