import React,{useState, useEffect} from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _ from 'lodash';

const RoleForm = (props) => {

   const [data,setData] = useState({id:null, title: "", active: 1});
   const [loading, setLoading] = useState(false);

   const fetchRecord = (id) => {
      setLoading('Fetching data..');
      axios.get(Utils.apiUrl(`role/list?id=${id}`),Utils.apiHeaders())
      .then(res => {
         if(res.data.success && _.get(res,'data.data.0',false)){
            setData(res.data.data[0]);
            setLoading(false);
         }
      })
   };

   useEffect(() => {
      if(_.get(props,'id',0) > 0){
         fetchRecord(props.id);
      }
   },[]);

   const submitForm = (e) => {
     const frm = e.currentTarget;
     e.preventDefault();
 
     let frmdata = new FormData(frm);

     if(props.mode===2){
      frmdata.set('active',frm.active.checked ? "1" : "0");
     }

     (props.mode===1 ? axios.post : axios.put)(Utils.apiUrl(`role/${props.mode===1 ? 'add' : `edit/${data.id}`}`),frmdata,Utils.apiHeaders())
     .then(res => {
        if(res.data.success){
           window.alert("Record saved successfully!");
           props.onSubmit();
           props.onClose();
        }else{
           window.alert(res.data.message);
        }
     }) 
 
   };
 
   
   return (<div className="modal fade show d-block" data-backdrop={true}>
      <div className="modal-dialog">
         <div className="modal-content">

            <form onSubmit={submitForm}>
                  
               <div className="modal-header">
                  <h4 className="modal-title">{props.mode===2 ? 'Edit' : 'Add'} Role</h4>
                  <button type="button" className="close" onClick={props.onClose}>&times;</button>
               </div>
               <div className="modal-body">
                  
                  {loading !== false && <div className="alert alert-light p-5 text-center">{loading} <div className="spinner-grow text-danger"></div></div>}
                  
                  {loading === false && <>

                     <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Role Title</label>
                        <input type="text" name="title" required className="form-control" placeholder="Enter Role Name" defaultValue={_.get(data,'title','')} />
                     </div>

                     {props.mode === 2 && <div className="custom-control custom-switch">
                        <input type="checkbox" className="custom-control-input" id={`active${props.id}`} name="active" value="1" defaultChecked={data.active} />
                        <label className="custom-control-label" htmlFor={`active${props.id}`}> Active State</label>
                     </div>}
                     
                  </>}
               </div>

               {loading === false && <div className="modal-footer">
                  <button type="submit" className="btn btn-primary">Submit</button>
                  <button type="button" className="btn btn-secondary" onClick={props.onClose}>Close</button>
               </div>}

            </form>
         </div>
      </div>
   </div>);

};

export default RoleForm;