import React,{useEffect, useContext} from 'react';
import UserContext from './../contexts/UserContext';
import Utils from './../Utils';

const Login = (props) => {

  const {goLogin} = useContext(UserContext);

  const loginSubmit = (e) => {
    const frm = e.currentTarget;
    e.preventDefault();

    const data = {
      user: frm.username.value,
      pass: frm.password.value
    };

    goLogin(data,(data) => {
      window.alert(data.message);
      if(data.success){
        window.location.href=process.env.PUBLIC_URL;
      }
    })
  };

  useEffect(() => {
    if(Utils.isLoggedIn()){
      window.location.href=process.env.PUBLIC_URL;
    }
  },[]);

  return (<div className="login-box">
  <div className="login-logo">
    <img src={`${process.env.PUBLIC_URL}/dist/img/logo2.png`} alt="AutoDidact Logo" className="brand-image elevation-3 img-fluid" />
  </div>
  <div className="card mt-0">
    <div className="card-body login-card-body">
      <p className="login-box-msg">Login to Admin Panel</p>

      <form method="post" onSubmit={loginSubmit}>
        <div className="input-group mb-3">
          <input type="text" className="form-control" placeholder="Username" name="username" required />
          <div className="input-group-append">
            <div className="input-group-text">
              <span className="fas fa-envelope"></span>
            </div>
          </div>
        </div>
        <div className="input-group mb-3">
          <input type="password" className="form-control" placeholder="Password" name="password" required />
          <div className="input-group-append">
            <div className="input-group-text">
              <span className="fas fa-lock"></span>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-8">
            <a href="forgot-password.html">I forgot my password</a>
          </div>
          <div className="col-4">
            <button type="submit" className="btn btn-primary btn-block">Sign In</button>
          </div>
        </div>
      </form>
    
    </div>
  </div>
</div>);

  }

  export default Login;